import { useState } from "react"
import { __ } from "@wordpress/i18n"
import {
  Button,
  PanelBody,
  TextControl,
  ToggleControl,
} from "@wordpress/components"

export function Settings() {
  const [isEnabled, setIsEnabled] = useState(false)
  const [title, setTitle] = useState("")
  const [content, setContent] = useState("")

  function onSaveSettings() {
    console.log("Settings saved", isEnabled, title, content)
  }

  return (
    <div className="my-plugin-settings">
      <PanelBody title={__("My Plugin Settings", "my-plugin")}>
        <ToggleControl
          label={__("Enable plugin", "my-plugin")}
          checked={isEnabled}
          onChange={(value) => setIsEnabled(value)}
        />

        <TextControl
          label={__("Title", "my-plugin")}
          value={title}
          onChange={(value) => setTitle(value)}
        />

        <TextControl
          label={__("Content", "my-plugin")}
          value={content}
          onChange={(value) => setContent(value)}
        />
        <Button isPrimary onClick={onSaveSettings}>
          {__("Save settings", "my-plugin")}
        </Button>
      </PanelBody>
    </div>
  )
}
